
.inputContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 25px;
}

.inputTitle {
    color: aquamarine;
    font-size: 20px;
    font-weight: 700;
}

.input {
    border: 3px solid aquamarine;
    border-radius: 10px;
    text-align: center;
    color: #2E2B2B;
    font-weight: 700;
    outline: none;
}

.inputError {
    border: 3px solid #6c0404;
    border-radius: 10px;
    text-align: center;
    color: #2E2B2B;
    background-color: #e87c7c;
    font-weight: 700;
    outline: none;
}

